export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      dateFormat=\"MM/yyyy\"\n      showMonthYearPicker\n      showFullMonthYearPicker\n      showFourColumnMonthYearPicker\n    />\n  );\n};\n";