export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      locale=\"pt-BR\"\n      showTimeSelect\n      timeFormat=\"p\"\n      timeIntervals={15}\n      dateFormat=\"Pp\"\n    />\n  );\n};\n";