export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      highlightDates={[subDays(new Date(), 7), addDays(new Date(), 7)]}\n      placeholderText=\"This highlights a week ago and a week from today\"\n    />\n  );\n};\n";