export default "() => {\n  const [startDate, setStartDate] = useState(null);\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      minDate={new Date()}\n      maxDate={addDays(new Date(), 5)}\n      placeholderText=\"Select a date between today and 5 days in the future\"\n    />\n  );\n};\n";