export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  const [endDate, setEndDate] = useState(null);\n  const onChange = (dates) => {\n    const [start, end] = dates;\n    setStartDate(start);\n    setEndDate(end);\n  };\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={onChange}\n      startDate={startDate}\n      endDate={endDate}\n      selectsRange\n      inline\n    />\n  );\n};\n";