export default "() => {\n  const [startDate, setStartDate] = useState(new Date(\"2024-08-01\"));\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      dateFormat=\"MM/yyyy\"\n      excludeDates={[new Date(\"2024-05-01\"), new Date(\"2024-06-01\")]}\n      showMonthYearPicker\n    />\n  );\n};\n";