export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n\n  let handleColor = (time) => {\n    return time.getHours() > 12 ? \"text-success\" : \"text-error\";\n  };\n\n  return (\n    <DatePicker\n      showTimeSelect\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      timeClassName={handleColor}\n    />\n  );\n};\n";