export default "() => {\n  const [selectedDates, setSelectedDates] = useState([new Date()]);\n  const onChange = (dates) => {\n    setSelectedDates(dates);\n  };\n  return (\n    <DatePicker\n      selectedDates={selectedDates}\n      selectsMultiple\n      onChange={onChange}\n      shouldCloseOnSelect={false}\n      disabledKeyboardNavigation\n    />\n  );\n};\n";