export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      showTimeSelect\n      timeFormat=\"HH:mm\"\n      timeIntervals={15}\n      timeCaption=\"time\"\n      dateFormat=\"MMMM d, yyyy h:mm aa\"\n    />\n  );\n};\n";