export default "() => {\n  const renderYearContent = (year) => {\n    const tooltipText = `Tooltip for year: ${year}`;\n    return <span title={tooltipText}>{year}</span>;\n  };\n  return (\n    <DatePicker\n      selected={new Date()}\n      renderYearContent={renderYearContent}\n      showYearPicker\n      dateFormat=\"yyyy\"\n    />\n  );\n};\n";