export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      dateFormatCalendar={\"MMM yyyy\"}\n      minDate={subMonths(new Date(), 6)}\n      maxDate={addMonths(new Date(), 6)}\n      showMonthYearDropdown\n    />\n  );\n};\n";