export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      popperClassName=\"some-custom-class\"\n      popperPlacement=\"top-end\"\n      popperModifiers={[\n        {\n          name: \"myModifier\",\n          fn(state) {\n            // Do something with the state\n            return state;\n          },\n        },\n      ]}\n    />\n  );\n};\n";