export default "() => {\n  const [startDate, setStartDate] = useState(new Date(\"2014/02/08\"));\n  const [endDate, setEndDate] = useState(new Date(\"2024/04/08\"));\n  return (\n    <>\n      <DatePicker\n        selected={startDate}\n        onChange={(date) => setStartDate(date)}\n        selectsStart\n        startDate={startDate}\n        endDate={endDate}\n        dateFormat=\"yyyy\"\n        showYearPicker\n      />\n      <DatePicker\n        selected={endDate}\n        onChange={(date) => setEndDate(date)}\n        selectsEnd\n        startDate={startDate}\n        endDate={endDate}\n        dateFormat=\"yyyy\"\n        showYearPicker\n      />\n    </>\n  );\n};\n";