export default "() => {\n  const [startDate, setStartDate] = useState(null);\n\n  return (\n    <>\n      <DatePicker\n        selected={startDate}\n        onChange={(date) => setStartDate(date)}\n        required\n        form=\"external-form\"\n      />\n      <form id=\"external-form\">\n        <input type=\"submit\" />\n      </form>\n    </>\n  );\n};\n";