export default "() => {\n  const [startDate, setStartDate] = useState(null);\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      includeDates={[new Date(), addDays(new Date(), 1)]}\n      placeholderText=\"This only includes today and tomorrow\"\n    />\n  );\n};\n";