export default "() => {\n  const [startDate, setStartDate] = useState(null);\n  const isWeekday = (date) => {\n    const day = getDay(date);\n    return day !== 0 && day !== 6;\n  };\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      filterDate={isWeekday}\n      placeholderText=\"Select a weekday\"\n    />\n  );\n};\n";