export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      excludeDates={[new Date(), subDays(new Date(), 1)]}\n      placeholderText=\"Select a date other than today or yesterday\"\n    />\n  );\n};\n";