export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      peekNextMonth\n      showMonthDropdown\n      showYearDropdown\n      dropdownMode=\"select\"\n    />\n  );\n};\n";