export default "() => {\n  const [dateRange, setDateRange] = useState([null, null]);\n  const [startDate, endDate] = dateRange;\n  return (\n    <DatePicker\n      selectsRange={true}\n      startDate={startDate}\n      endDate={endDate}\n      onChange={(update) => {\n        setDateRange(update);\n      }}\n      withPortal\n    />\n  );\n};\n";