export default "() => {\n  const [startDate, setStartDate] = useState(null);\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      minDate={subDays(new Date(), 5)}\n      placeholderText=\"Select a date after 5 days ago\"\n    />\n  );\n};\n";