export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      dayClassName={(date) =>\n        getDate(date) < Math.random() * 31 ? \"random\" : undefined\n      }\n    />\n  );\n};\n";