export default "() => {\n  const [startDate, setStartDate] = useState(new Date(\"2021/02/22\"));\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      dateFormat=\"I/R\"\n      locale=\"en-GB\"\n      excludeDateIntervals={[\n        { start: \"2021/02/08\", end: \"2021/02/14\" },\n        { start: \"2021/01/18\", end: \"2021/01/24\" },\n      ]}\n      showWeekNumbers\n      showWeekPicker\n    />\n  );\n};\n";