export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      timeInputLabel=\"Time:\"\n      dateFormat=\"MM/dd/yyyy h:mm aa\"\n      showTimeInput\n    />\n  );\n};\n";