export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      maxDate={addDays(new Date(), 5)}\n      placeholderText=\"Select a date before 5 days in the future\"\n    />\n  );\n};\n";