export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      showTimeSelect\n      showTimeSelectOnly\n      timeIntervals={15}\n      dateFormat=\"h:mm aa\"\n      showTimeCaption={false}\n    />\n  );\n};\n";